import React, { useState, useEffect } from "react";
import * as server from "../server";
import GroupListItem from "./GroupListItem";
import * as SC from "../styled";
import * as utils from "../utils";

const isOdd = (n) => (n & 1) === 1;

const getCharHasList = (groupList, groupData) => {
  let charHasList = [];
  groupList.map((groupItemName) =>
    groupData.map((groupItem) => {
      if (groupItem.name === groupItemName) {
        charHasList = [...charHasList, groupItem];
      }
    })
  );
  return charHasList;
};

const GroupList = (props) => {
  const [isLoading, setIsLoading] = useState(true);
  const [errs, setErrs] = useState([]);

  const [groupList, setGroupList] = useState(props.char[props.groupName]);
  const [groupData, setGroupData] = useState(null);
  const [groupTracker, setGroupTracker] = useState(null);

  const [editMode, setEditMode] = useState(false);
  const [isAddingNewGroupItem, setIsAddingNewGroupItem] = useState(false);

  const handleGroupTracker = () => {
    switch (props.groupName) {
      case "gear":
        let maxLoad =
          10 +
          utils.getJSONFieldByName(props.char.abilities, "Strength", "score");
        let currentLoad = getCharHasList(groupList, groupData)
          .map((gear) => gear.weight || 0)
          .reduce((a, b) => a + b, 0);
        setGroupTracker(`${currentLoad}/${maxLoad}üéí`);
        if (currentLoad > maxLoad) {
          handleErrs("OVER_MAX", "ADD");
        }
        break;
      case "spells":
        let maxSpellLevels = 1 + props.char.level;
        let currentSpellLevels = getCharHasList(groupList, groupData)
          .map((spell) => spell.level || 0)
          .reduce((a, b) => a + b, 0);
        setGroupTracker(`${currentSpellLevels}/${maxSpellLevels}‚ú®`);
        if (currentSpellLevels > maxSpellLevels) {
          handleErrs("OVER_MAX", "ADD");
        } else {
          handleErrs("OVER_MAX", "REMOVE");
        }
        break;
      case "moves":
        setGroupTracker(getCharHasList(groupList, groupData).length);
        break;
      default:
        break;
    }
  };

  const toggleEditMode = (e) => {
    setEditMode(!editMode);
  };

  const handleErrs = (err, action) => {
    let newErrs = errs;
    if (action === "ADD" && !errs.includes(err)) {
      newErrs = [...newErrs, err];
    }

    if (action === "REMOVE" && errs.includes(err)) {
      newErrs = newErrs.filter((e) => e !== err);
    }
    setErrs(newErrs);
  };

  const handleUpdateScore = (action) => {
    // console.log(action);
    let newGroupData;
    switch (action.message) {
      case "UPDATE":
        newGroupData = groupData;
        for (let [i, g] of Object.entries(newGroupData)) {
          if (g.name === action.target) {
            newGroupData[i].score = action.newValue;
          }
        }
        setGroupData(newGroupData);
        break;
      default:
        console.error("action message not recognized: ", action.message);
        return;
    }
  };

  const handleUpdateGroupList = (action) => {
    let newGroupList;
    switch (action.message) {
      case "REMOVE":
        if (groupList.includes(action.newValue)) {
          newGroupList = groupList.filter((e) => e !== action.newValue);
        }
        break;
      case "ADD":
        if (!groupList.includes(action.newValue)) {
          newGroupList = [...groupList, action.newValue];
        }
        break;
      default:
        console.error("action message not recognized: ", action.message);
        return;
    }

    // console.log("newGroupList", newGroupList);
    setGroupList(newGroupList);
  };

  const handleSaveChanges = () => {
    if (errs[0]) {
      setGroupList(props.char[props.groupName]);
      return;
    }
    let newCharState = { ...props.char, [props.groupName]: groupList };
    props.setChar(newCharState);
    server.saveCharacterData(newCharState);
    toggleEditMode();
  };

  useEffect(() => {
    if (isLoading && !groupData) {
      const getAllChars = async () => {
        // console.log("server.getGroupData(props.groupName)");
        let newGroupData = await server.getGroupData(props.groupName);
        if (props.groupName === "abilities") {
          newGroupData.data = props.char.abilities;
        }
        setGroupData(newGroupData.data);
        setIsLoading(false);
      };
      getAllChars();
    }

    if (!isLoading) handleGroupTracker();
  }, [editMode, props.char.level, isLoading, groupList, errs]);

  return (
    <div style={{}}>
      <div
        style={{
          display: "flex",
          marginRight: "0.2rem"
        }}
      >
        <div className="group-item-label" style={{ textAlign: "center" }}>
          <b style={{ borderBottom: "1px solid gray", padding: "0 2rem" }}>
            {String(props.groupName).toUpperCase()}
          </b>
        </div>
        <div
          style={{
            padding: "0.4rem 0.2rem",
            color: `${errs.includes("OVER_MAX") ? "red" : "black"}`
          }}
        >
          {groupTracker && groupTracker}
        </div>
        <SC.ToggleLight toggle={!editMode} onClick={() => toggleEditMode()}>
          <span role="image">{editMode ? "‚ùå" : "‚öôÔ∏è"}</span>
        </SC.ToggleLight>
        {editMode && (
          <SC.ToggleLight
            toggle={errs.length === 0}
            onClick={(e) => handleSaveChanges()}
          >
            <span role="image">{errs[0] ? "‚õî" : "üíæ"}</span>
          </SC.ToggleLight>
        )}
      </div>
      {!isLoading && (
        <div className="fwmp">
          {!isAddingNewGroupItem &&
            Object.values(groupData).map(
              (groupItem) =>
                (groupList.includes(groupItem.name) ||
                  editMode ||
                  props.groupName === "abilities") && (
                  <div
                    id={`group-list-item-container-${groupItem.name}`}
                    key={`group-list-item-container-${groupItem.name}`}
                    className="group-list-item-container"
                  >
                    <GroupListItem
                      groupName={props.groupName}
                      labelValue={groupItem.name}
                      subtitle={groupItem.level || ""}
                      level={groupItem.level}
                      price={groupItem.price}
                      description={groupItem.description}
                      source={groupItem.source || ""}
                      webURL={groupItem.webURL || ""}
                      playbooks={groupItem.playbooks || ""}
                      rollWith={groupItem.rollWith || ""}
                      editMode={editMode}
                      handleUpdateGroupList={handleUpdateGroupList}
                      handleUpdateScore={handleUpdateScore}
                      hasItem={groupList.includes(groupItem.name)}
                      type={groupItem.type || ""}
                      tags={groupItem.tags || ""}
                      weight={groupItem.weight || ""}
                      score={groupItem.score || ""}
                    />
                  </div>
                )
            )}
        </div>
      )}
      <br />
    </div>
  );
};

export default GroupList;
