import styled from "styled-components";

export const ToggleLight = styled.div`
  cursor: pointer;
  padding: 0.3rem;
  margin-right: 0.1rem;
  border: 1px solid gray;
  border-radius: 5px;
  width: 1.25rem;
  height: 1.25rem;
  text-align: center;
  text-justify: center;
  justify-content: center;
  align-content: center;
  background-color: ${(props) =>
    props.toggle ? `rgb(220, 230, 220)` : `rgb(230, 220, 220)`};

  :hover {
    border-bottom: 1px solid black;
    border-right: 1px solid black;
  }
`;

export const ToggleTag = styled(ToggleLight)`
  width: auto;
`;
