import React, { useState, useEffect } from "react";
import * as server from "../server";

import List from "../components/List";
import GroupList from "../components/GroupList";
import GroupListItem from "../components/GroupListItem";

const getJSONFieldByName = (obj, name, field) => {
  let res;
  obj.map((entry) => {
    if (entry.name === name) {
      res = entry[field];
    }
  });
  return res;
};

const xpNeeded = (level) => level * 7 + level;

const CharacterSheet = (props) => {
  const [displayMode, setDisplayMode] = useState("compact");

  return (
    <div className="App">
      <div className={displayMode}>
        {/* <Formik initialValues={char} onSubmit={() => {}}>
          {() => (
            <Form>
              <div className="single-item-container">
                <Field
                  label="Name"
                  name="name"
                  field="name"
                  subtitle="Name"
                  oneLine={true}
                  labelValue={char.name}
                  component={GroupListItem}
                />
              </div>
            </Form>
          )}
        </Formik> */}

        <div className="single-item-container">
          <GroupListItem
            field="name"
            subtitle="Name"
            labelValue={props.char.name}
            oneLine={true}
          />
        </div>

        <div className="single-item-container">
          <GroupListItem
            field="race"
            subtitle="Race"
            labelValue={props.char.race}
            oneLine={true}
          />
        </div>
        <div className="single-item-container">
          <GroupListItem
            field="playbook"
            subtitle="Playbook"
            labelValue={props.char.playbook}
            source={`${props.char.playbook}`}
            webURL={props.char.webURL}
            oneLine={true}
          />
        </div>

        <div className="single-item-container">
          <GroupListItem
            field="alignment"
            subtitle="Alignment"
            labelValue={props.char.alignment.name}
            oneLine={true}
          />
        </div>

        <div className="single-item-container">
          <GroupListItem
            field="level"
            subtitle="Level"
            labelValue={props.char.level}
            oneLine={true}
          />
        </div>

        <div className="single-item-container">
          <GroupListItem
            field="xp"
            subtitle="XP"
            labelValue={`${props.char.xp} / ${xpNeeded(props.char.level + 1)}`}
            oneLine={true}
          />
        </div>

        <div className="single-item-container">
          <GroupListItem
            field="hp"
            subtitle="HP"
            labelValue={
              props.char.basehp +
              getJSONFieldByName(props.char.abilities, "Constitution", "score")
            }
            oneLine={true}
          />
        </div>
      </div>
      <br />
      <List
        list={props.char.abilities}
        handleSave={props.handleSave}
        listName="abilities"
      />
      {/* <GroupList
        groupName="abilities"
        char={props.char}
        setChar={props.setChar}
      /> */}

      <div style={{ padding: 0, margin: 0 }}>
        <div className="group-item-labelValue" style={{ textAlign: "center" }}>
          <b style={{ borderBottom: "1px solid gray", padding: "0 2rem" }}>
            BONDS
          </b>
        </div>

        <div className="fwmp">
          {props.char.bonds.map((bond) => (
            <div key={bond.description} className="single-item-container">
              <GroupListItem
                subtitle={bond.description}
                field="bonds"
                oneLine={true}
              />
            </div>
          ))}
        </div>
      </div>
      <br />

      <GroupList groupName="moves" char={props.char} setChar={props.setChar} />

      <GroupList groupName="spells" char={props.char} setChar={props.setChar} />

      <GroupList groupName="gear" char={props.char} setChar={props.setChar} />
    </div>
  );
};

export default CharacterSheet;
