import React, { useState, useEffect } from "react";
import CharacterSheet from "./views/CharacterSheet";
import UserSelect from "./views/UserSelect";

import * as server from "./server";

import "./styles.css";

export default function App() {
  const isSiteUpdating = false;

  const [isLoading, setIsLoading] = useState(true);
  const [allUsers, setAllUsers] = useState(null);
  const [currentUser, setCurrentUser] = useState(null);
  const [charName, setCharName] = useState("Levi");
  const [char, setChar] = useState(null);
  const [userId, setUserId] = useState(null);

  useEffect(() => {
    if (isLoading && !allUsers) {
      const getUsers = async () => {
        let users = await server.getAllUsers();
        console.log("setAllUsers: ", users);
        setAllUsers(users);
        setIsLoading(false);
      };
      getUsers();
    }
    // if (charName && allUsers) {
    //   for (let [i, ch] of Object.entries(...allUsers)) {
    //     if (ch.name === charName) {
    //       console.log("setChar: ", ch);
    //       setUserId(i);
    //       setChar(ch);
    //     }
    //   }
    // }
  }, [charName, allUsers]);

  const handleSave = async (field, newValue) => {
    let newChar = char;
    newChar[field] = newValue;
    let data = await server.saveCharacterData(newChar);
    console.log(data);
    setChar(data);
  };

  const handleUserChange = (userName) => {
    console.log("Searching for user: ", userName);
    console.log("allUsers: ", allUsers);
    for (let user of allUsers) {
      console.log("user: ", user);
      if (user.userName === userName) {
        setCurrentUser(user);
        console.log("setChar: ", user.characters[0]);
        server.setUserId(user.id);
        setChar(user.characters[0]);
      }
    }
    // for (let user of allUsers) {
    //   console.log(user);
    //   if (user.userName === userName) {
    //     console.log("matched! userName = ", user.userName);
    //     setChar(user.characters[0]);
    //     server.setUserId(user.id);
    //   }
    // }
    // setUserId(newUserID)};
  };
  return (
    <div>
      {isSiteUpdating && <Alert />}

      {currentUser && <div>User: {currentUser.userName}</div>}

      {allUsers && !char && (
        <UserSelect
          allUsers={allUsers}
          setAllUsers={setAllUsers}
          handleUserChange={handleUserChange}
        />
      )}

      {/* {userId && (
        <div>
          {allChars.map((chars) => {
            return (
              <div key={Math.random()}>
                {chars.map((aChar) => {
                  return (
                    <div key={Math.random()} value={aChar.name}>
                      {aChar.name}
                    </div>
                  );
                })}
              </div>
            );
          })}
        </div>
      )} */}
      {char && (
        <CharacterSheet char={char} setChar={setChar} handleSave={handleSave} />
      )}

      {isLoading && <div>loading...</div>}
    </div>
  );
}

const Alert = () => (
  <div
    style={{
      backgroundColor: "rgb(13, 30, 26)",
      color: "darkorange",
      borderBottom: "6px double orange",
      padding: "1rem"
    }}
  >
    <span>{`ALERT: I'm logged in and am developing this site `}</span>
    <span style={{ borderBottom: "3px double gray" }}>{`right now`}</span>.
    <span>{` Things may be broken. If so, refresh or try again later.`}</span>
  </div>
);
