import React, { useState } from "react";
import * as server from "../server";

const UserSelect = (props) => {
  const [isNewUser, setIsNewUser] = useState(false);
  const [isSubmitting, setIsSubmitting] = useState(false);
  const [newUserName, setNewUserName] = useState("");

  const toggleIsNewUser = () => setIsNewUser(!isNewUser);

  const handleUserInput = (userName) => setNewUserName(userName);

  const submitNewUser = async () => {
    setIsSubmitting(true);
    let allUsers = await server.submitNewUser(newUserName);
    if (allUsers) {
      props.setAllUsers(allUsers);
      props.handleUserChange(newUserName);
    }
  };

  return (
    <div>
      <h3 style={{ padding: "1rem", borderBottom: "1px solid gray" }}>
        Accounts
      </h3>
      <div className="fwmp">
        {props.allUsers && props.allUsers.map((user) => (
          <div
            onClick={(e) => props.handleUserChange(user.userName)}
            className="single-item-container"
            key={Math.random()}
          >
            <b>{user.userName}</b>

            <div>
              {user.characters.map((character, i) => (
                <div className="indented" key={Math.random()}>
                  {character.name}
                </div>
              ))}
            </div>
          </div>
        ))}

        <div
          onClick={toggleIsNewUser}
          className={`single-item-container ${
            isNewUser ? "selected" : "unselected"
          }`}
        >
          New User
        </div>
      </div>
      {isNewUser && (
        <div
          style={{
            display: "flex",
            minWidth: "100vw",
            alignContent: "center",
            justifyContent: "center"
          }}
        >
          <div className="single-item-container">
            <input
              onChange={(e) => {
                handleUserInput(e.target.value);
              }}
              style={{
                margin: "1rem 0",
                minWidth: "99%",
                textAlign: "center"
              }}
              placeholder="User Name"
            />
            <div
              onClick={(e) => submitNewUser()}
              className={`clickable ${isNewUser ? "selected" : "unselected"}`}
              style={{
                padding: "1rem 0",
                minWidth: "100%",
                textAlign: "center"
              }}
            >
              CREATE
            </div>
          </div>
        </div>
      )}
    </div>
  );
};

export default UserSelect;
