export const submitNewUser = async (userName) => {
  console.log("submitting: ", `${URL_BASE}/newUser/${userName}`);
  const res = await axios.post(`${URL_BASE}/newUser/${userName}`, "", {
    headers: {
      "Content-Type": "text/plain"
    }
  });
  console.log(res);
  return res;
};
