import React, { useEffect, useState } from "react";
import * as SC from "../styled";
import ReactMarkdown from "react-markdown";

const statScoreToModScore = (statScore) => {
  if (!statScore) return "";
  if (statScore < 4) return "-3";
  if (statScore < 6) return "-2";
  if (statScore < 9) return "-1";
  if (statScore < 13) return "0";
  if (statScore < 16) return "+1";
  if (statScore < 18) return "+2";
  return "+3";
};

const IncrementButton = (props) => {
  return (
    <div>
      <SC.ToggleLight
        toggle={props.hasItem}
        onClick={(e) =>
          props.handleUpdateScore({
            group: props.groupName,
            newValue: props.score + 1,
            message: "UPDATE",
            target: props.labelValue
          })
        }
      >
        <span>+</span>
      </SC.ToggleLight>
    </div>
  );
};

const Score = (props) => {
  useEffect(() => {
    console.log("props.score", props.score);
  }, [props.score]);
  if (!props.score) return "";
  return (
    <div
      style={{
        display: "flex",
        cursor: "pointer",
        fontSize: "0.9rem",
        color: "rgb(10, 10, 10)",
        margin: "0 0.2rem"
      }}
    >
      <div style={{ margin: "0 0.2rem" }}>
        <b>{props.score}</b>
      </div>
      <IncrementButton
        groupName={props.groupName}
        labelValue={props.labelValue}
        score={props.score}
        handleUpdateScore={props.handleUpdateScore}
      />
      <div style={{ width: "2rem" }}>
        {statScoreToModScore(props.score) &&
          `(${statScoreToModScore(props.score)})`}
      </div>
    </div>
  );
};

const GroupListItem = (props) => {
  const [showAll, setShowAll] = useState(false);
  const [editMode, setEditMode] = useState(false);

  if (props.groupName === "abilities") {
    // console.log(`props.groupName === "abilities"`, props);
  }

  const toggleEditMode = (mode = null) => {
    setEditMode(mode || !editMode);
  };

  const handleUserInput = (field, newValue) => {
    console.log(field, newValue);
  };

  const toggleShowAll = () => {
    // if (props.oneLine) {
    //   console.log("edit: ", props.field);
    //   toggleEditMode();
    // }
    if (
      document.getElementById(`group-list-item-container-${props.labelValue}`)
    ) {
      if (!showAll) {
        document.getElementById(
          `group-list-item-container-${props.labelValue}`
        ).stylestyle = { width: "100%", maxHeight: "auto" };
      } else {
        document.getElementById(
          `group-list-item-container-${props.labelValue}`
        ).style = { width: "46%", maxHeight: "3rem" };
      }
    }
    if (!props.oneLine) setShowAll(!showAll);
    toggleEditMode();
  };

  let url = null;
  const getUrl = () => {
    if (props.source === "Custom") return null;
    if (props.source === "Core") {
      url = `https://www.dungeonworldsrd.com/`;
    }

    if (props.playbooks && props.playbooks[0]) {
      url += `classes/${props.playbooks[0]}/#${String(
        props.labelValue
      ).replaceAll(" ", "_")}`;
    } else {
      if (props.type === "Weapon") {
        url += `Equipment/#Weapon_List`;
      } else {
        url += `Equipment/#${String(props.type).replaceAll(" ", "_")}`;
      }
    }
    return url;
  };
  url = getUrl();

  const ItemSelectButton = (props) => {
    if (props.groupName === "abilities") return <div />;
    return (
      <div>
        {props.editMode ? (
          <SC.ToggleLight
            toggle={props.hasItem}
            onClick={(e) =>
              props.handleUpdateGroupList({
                group: props.groupName,
                newValue: props.labelValue,
                message: props.hasItem ? "REMOVE" : "ADD"
              })
            }
          >
            <span role="image">{props.hasItem ? "‚úîÔ∏è" : "‚ùå"}</span>
          </SC.ToggleLight>
        ) : (
          <div />
        )}
      </div>
    );
  };

  const Subtitle = () => {
    if (props.oneLine)
      return (
        <div
          className="clickable"
          onClick={toggleShowAll}
          style={{
            alignContent: "flex-end",
            justifyContent: "flex-end",
            fontSize: "0.8rem",
            color: "rgb(10, 10, 10)",
            padding: "0 0.2rem"
          }}
        >
          {props.subtitle && (
            <ReactMarkdown>{String(props.subtitle)}</ReactMarkdown>
          )}
        </div>
      );
    return (
      <div
        className="clickable"
        onClick={toggleShowAll}
        style={{
          display: "flex",
          fontSize: "0.8rem",
          color: "rgb(10, 10, 10)",
          padding: "0 0.2rem"
        }}
      >
        <div style={{ display: "flex", flexGrow: 1 }}>
          {props.playbooks[0] && (
            <div style={{ marginRight: "0.2rem" }}>
              <ReactMarkdown>{String(props.playbooks)}</ReactMarkdown>
            </div>
          )}
          {props.subtitle && (
            <div style={{ marginRight: "0.2rem" }}>
              <ReactMarkdown>{String(props.subtitle)}</ReactMarkdown>
            </div>
          )}
          {props.tags[0] && (
            <div style={{ marginRight: "0.2rem" }}>
              <ReactMarkdown>{String("*" + props.tags + "*")}</ReactMarkdown>
            </div>
          )}
        </div>
        <div
          style={{
            display: "flex",
            alignContent: "flex-end",
            justifyContent: "flex-end"
          }}
        >
          {props.weight && (
            <ReactMarkdown>{String(props.weight + "üéí, ")}</ReactMarkdown>
          )}

          {props.price && (
            <ReactMarkdown>{String(props.price + "üí∞")}</ReactMarkdown>
          )}
        </div>
      </div>
    );
  };

  return (
    <div className={showAll ? "selected" : "unselected"}>
      {/* {editMode && (
        <input onChange={(e) => handleUserInput(props.field, e.target.value)} />
      )} */}
      <div style={{ display: "flex" }}>
        <ItemSelectButton />
        <div
          className="group-item-labelValue clickable"
          onClick={toggleShowAll}
        >
          <div style={{ display: "flex" }}>
            <div>
              <b>{props.labelValue}</b>
            </div>
          </div>
          <Subtitle />
        </div>
        <Score
          groupName={props.groupName}
          labbelValue={props.labelValue}
          handleUpdateScore={props.handleUpdateScore}
          onClick={toggleShowAll}
          style={{}}
          score={props.score}
        />

        {props.rollWith && (
          <SC.ToggleLight
            onClick={() => alert("Dice rolling not yet available.")}
          >
            üé≤
          </SC.ToggleLight>
        )}
        {showAll && url && props.groupName !== "abilities" && (
          <SC.ToggleLight toggle={true}>
            <a
              href={url}
              rel="noopener noreferrer"
              target="_blank"
              style={{ textDecoration: "none" }}
            >
              <span role="image">üîó</span>
            </a>
          </SC.ToggleLight>
        )}
      </div>
      {showAll && props.description && (
        <div
          className="clickable"
          onClick={toggleShowAll}
          style={{
            marginLeft: "0.1rem",
            fontSize: "0.9rem"
          }}
        >
          <ReactMarkdown>{String(props.description)}</ReactMarkdown>
        </div>
      )}
    </div>
  );
};

export default GroupListItem;
