import React, { useState, useEffect, useReducer } from "react";
import * as server from "../server";

const reducer = (state, action) => {
  switch (action.type) {
    case "UPDATE":
      console.log(action.i, action.field, action.newValue);
      let newState = [...state];
      newState[action.i][action.field] = action.newValue;
      console.log(newState);
      return newState;
    default:
      return state;
  }
};

const List = (props) => {
  const [state, dispatch] = useReducer(reducer, props.list);

  console.log(props.listName);
  console.log(state);

  const handleUpdate = (i, field, newValue) => {
    dispatch({
      type: "UPDATE",
      i: i,
      field: field,
      newValue: newValue
    });
    props.handleSave(props.listName, state);
  };

  useEffect(() => {}, [state]);

  return (
    <div>
      <div>{String(props.listName).toUpperCase()}</div>
      <div className="fwmp">
        {state.length > 0 &&
          state.map((item, i) => (
            <div key={item.name} className={`group-item c`}>
              <b>{item.name}</b>
              <div
                className={`c`}
                style={{
                  display: "flex"
                }}
              >
                <div
                  className={`group-item`}
                  onClick={(e) => handleUpdate(i, "score", item.score - 1)}
                >
                  -
                </div>
                <div>{item.score}</div>
                <div
                  className={`group-item`}
                  onClick={(e) => handleUpdate(i, "score", item.score + 1)}
                >
                  +
                </div>
              </div>
            </div>
          ))}
      </div>
    </div>
  );
};

export default List;
